const quizQuestionsBlock = document.querySelector('.quiz__questions');
const quizHeader = document.querySelector('.quiz__header');
const quizFooter = document.querySelector('.quiz__footer');
const resultBlock = document.querySelector('.quiz__result');
const showPrevBtn = quizFooter.querySelector('#prev-question');
const showNextBtn = quizFooter.querySelector('#next-question');
const showResultsBtn = quizFooter.querySelector('#submit');
let quizQuestionsResult = document.querySelector('.quiz__results');
let quizQuestionAnswers = ``;
let quizRecordedAnswers = [];

const Quiz = (quizName) => {
  this.quizName = quizName;
  this.questions = [];
  this.id = ``;
}

Quiz.prototype.renderQuestion = (question) => {
  Quiz.id = question.id;
  renderQuizNumber(question);
  renderQuizAnswers(question);
  const quizQuestionTemplate = `
    <li class="quiz__question" id="${question.id}">
      <h3 class="heading heading--m">${question.questionString}</h3>
      ${quizQuestionAnswers}
    </li>`
  quizQuestionsBlock.innerHTML = quizQuestionsBlock.innerHTML + quizQuestionTemplate;
  setDefaultAnswer(question);
  setDisabledBtns();
};

const renderQuizAnswers = (question) => {
  question.choices.forEach(item => {
    const quizAnswersTemplate = `
    <label class="quiz__answer">
        <input class="visually-hidden" name="${item.name}" value="${item.value}" type="radio">
        <span>${item.text}</span>
    </label>`
    quizQuestionAnswers = quizQuestionAnswers + quizAnswersTemplate;
  });
};

const renderQuizNumber = (question) => {
  const quizQuestionNumber = quizHeader.querySelector('.quiz__num');
  const quizQuestionTotal = quizHeader.querySelector('.quiz__total');
  quizQuestionNumber.innerHTML = question.id;
  quizQuestionTotal.innerHTML = allQuestions.length;
}

const setDefaultAnswer = (question) => {
  const allAnswers = document.querySelectorAll('input[type="radio"]');
  const checkId = quizRecordedAnswers[question.id - 1];
  if (checkId) {
    allAnswers[checkId.id].setAttribute("checked", "checked");
  } else {
    allAnswers[0].setAttribute("checked", "checked");
  }
};

const setDefaultButtons = () => {
  showNextBtn.addEventListener('click', (e) => {
    e.preventDefault();
    e.stopPropagation();
    saveAnswer();
    deleteQuestion();
    quiz.renderQuestion(allQuestions[Quiz.id]);
  });
  showPrevBtn.addEventListener('click', (e) => {
    e.preventDefault();
    e.stopPropagation();
    saveAnswer();
    deleteQuestion();
    quiz.renderQuestion(allQuestions[Quiz.id - 2]);
  });
  showResultsBtn.addEventListener('click', (e) => {
    e.preventDefault();
    e.stopPropagation();
    saveAnswer();
    deleteQuestion();
    snowResult();
  });
};

const setDisabledBtns = () => {
  showPrevBtn.removeAttribute("disabled", "disabled");
  showNextBtn.removeAttribute("disabled", "disabled");
  showResultsBtn.setAttribute("disabled", "disabled");

  if (Quiz.id == 1) {
    showPrevBtn.setAttribute("disabled", "disabled");
  }

  if (Quiz.id == allQuestions.length) {
    showNextBtn.setAttribute("disabled", "disabled");
  }

  if (quizRecordedAnswers.length === allQuestions.length - 1) {
    showResultsBtn.removeAttribute("disabled", "disabled");
  }
}

const deleteQuestion = () => {
  quizQuestionsBlock.innerHTML = ``;
  quizQuestionAnswers = ``;
}

const saveAnswer = () => {
  const answerText = document.querySelector('input[type="radio"]:checked').value;
  let answer = {
    id: Quiz.id,
    value: answerText
  }
  quizRecordedAnswers[Quiz.id - 1] = answer;
}

const snowResult = () => {
  let result = [];

  resultBlock.classList.remove('visually-hidden');
  quizFooter.classList.add('visually-hidden');
  quizHeader.classList.add('visually-hidden');
  resultBlock.querySelector('#result');

  const counter = quizRecordedAnswers.reduce((o, i) => {
    if (!o.hasOwnProperty(i.value)) {
      o[i.value] = 0;
    }
    o[i.value]++;
    return o;
  }, {});
  let unsortedResult = Object.keys(counter).map((value) => {
    return {
      value: value,
      sum: counter[value]
    };
  });

  result = unsortedResult.sort((a, b) => a.sum > b.sum ? -1 : 1);
  console.log('result', result);

  const resultName = resultBlock.querySelector('#result');
  const resultDescription = resultBlock.querySelector('#desc');

  if (result[0].sum > result[1].sum) {
    const answer = allAnswers.filter(item => item.value == result[0].value);
    resultName.innerHTML = `Итак, ваш стиль – ` + result[0].value + `!`;
    resultDescription.innerHTML = answer[0].description;
  } else if (result[0].sum === result[1].sum) {

    const answer1 = allAnswers.filter(item => item.value == result[0].value);
    const answer2 = allAnswers.filter(item => item.value == result[1].value);
    resultName.innerHTML = `Итак, ваш стиль – ` + result[0].value + ` или ` + result[1].value + `!`;
    const desc1 = document.createElement("p");
    desc1.innerHTML = result[0].value + `: ` + answer1[0].description;
    resultDescription.appendChild(desc1);
    const desc2 = document.createElement("p");
    desc2.innerHTML = result[1].value + `: ` + answer2[0].description;
    resultDescription.appendChild(desc2);
  }

};

var quiz = new Quiz('My Quiz');
quiz.renderQuestion(allQuestions[0]);
setDefaultButtons();
